import React, {useRef} from "react";
import style from "./OnSubmitModal.module.scss"
import {svgIcons} from "../../../assets/svg/svgIcons";
import {translate} from "../../../utils/lang";
import {ButtonUI} from "../../common/ButtonUI/ButtonUI";
import clsx from "clsx";
import {useOutsideClick} from "../../../hooks/useOutsideClick";
import {useAppDispatch, useAppSelector } from "../../../redux-toolkit-store/hooks";
import {selectLang, selectShowModal, selectThemeType, setShowModal} from "../../../redux-toolkit-store/appSlice";

export const OnSubmitModal = () => {
    const lang = useAppSelector(selectLang);
    const dispatch = useAppDispatch();
    const onCloseHandler = () => {
        dispatch(setShowModal({show: false, type: ""}))
    };
    const showModal = useAppSelector(selectShowModal);
    const themeType = useAppSelector(selectThemeType);
    const ref = useRef<HTMLDivElement>(null);
    useOutsideClick(ref, () => dispatch(setShowModal({show: false, type: ''})));

    return (
        <div className={clsx({
            [style.onSubmitModal]: true,
            [style.onSubmitModal_light]: themeType === "light",
            [style.onSubmitModal_dark]: themeType === "dark",
            [style.onSubmitModal_show]: showModal.show && showModal.type === 'on submit',
        })}>
            <div className={style.content}
                 ref={ref}
            >
                <button className={style.close}
                        onClick={onCloseHandler}
                >
                    {svgIcons.close}
                </button>
                <p className={style.title}>
                    {translate("Thanks for the request, we will contact you soon", lang)}
                </p>

                <ButtonUI text="Go back"
                          className={style.btn}
                          onClick={onCloseHandler}
                />
            </div>
        </div>
    )
}