import React, {FC, useEffect, useRef, useState} from "react";
import {Container} from "../common/Container/Container";
import style from "./dds.module.scss"
import {translate} from "../../utils/lang";
import dds from "../../assets/png/DDS.png";
import {ButtonUI} from "../common/ButtonUI/ButtonUI";
import {INeedWebsiteModal} from "../A3_Modals/INeedWebsiteModal/INeedWebsiteModal";
import {useMediaQuery} from "@mui/material";
import {ButtonAnimation} from "../common/ButtonAnimation/ButtonAnimation";
import {useAppDispatch, useAppSelector} from "../../redux-toolkit-store/hooks";
import {selectLang, selectShowModal, setShowModal} from "../../redux-toolkit-store/appSlice";

interface IDds {
    x: number
    y: number
}

export const Dds: FC<IDds> = ({x, y}) => {
    const lang = useAppSelector(selectLang);
    const showModal = useAppSelector(selectShowModal);
    const dispatch = useAppDispatch();
    const textLabel = translate(
        "From turnkey websites to blockchain solutions and NFTs, we do everything you need in today's business.",
        lang
    );

    const isDesktop = useMediaQuery('(min-width:1200px)');
    const ref = useRef<HTMLDivElement>(null);
    const [alfaX, setAlfaX] = useState(0);
    const [alfaY, setAlfaY] = useState(0);

    useEffect(() => {
        if (ref && ref.current) {
            const x0 = ref.current.getBoundingClientRect().x;
            const y0 = ref.current.getBoundingClientRect().y;
            const w = ref.current.getBoundingClientRect().width;
            const h = ref.current.getBoundingClientRect().height;
            const x_center = x0 + w / 2;
            const y_center = y0 + h / 2;

            const deltaY = -(y - y_center);
            const deltaX = x - x_center;

            let alfaX = 0.05 * deltaY;
            alfaX = (alfaX <= -45)
                ? -45
                : (alfaX >= 45)
                    ? 45
                    : alfaX;
            setAlfaX(alfaX);

            let alfaY = 0.05 * deltaX;
            alfaY = (alfaY <= -45)
                ? -45
                : (alfaY >= 45)
                    ? 45
                    : alfaY;
            setAlfaY(alfaY);


        }
    }, [x, y, isDesktop]);


    return (
        <Container classNameInner={style.dds}>
            <div className={style.firstBlock}
                 ref={ref}
                //data-aos="fade-up"
            >
                <img src={dds} alt=""
                     style={
                         isDesktop
                             ? {transform: `rotateX(${alfaX}deg) rotateY(${alfaY}deg)`}
                             : {}
                     }
                />
            </div>

            <div className={style.secondBlock}>

                <h1 className={style.title}
                    //data-aos="fade-up"
                >
                    <p data-aos="fade-right">Digital</p>
                    <p data-aos="fade-right" data-aos-delay="300">Nova</p>
                </h1>

                <p className={style.text}
                   data-aos="fade-up"
                >
                    {textLabel}
                </p>

                <div className={style.btnWrapper}
                     data-aos="fade-up"
                >
                    <ButtonUI text="i need a website"
                              className={style.btn}

                              onClick={() => dispatch(setShowModal({show: true, type: 'i need a website'}))}
                    />
                    <ButtonAnimation className={style.buttonAnimation}/>
                </div>

            </div>

        </Container>
    )
}