export const translate = (str: string, lang: string):string => {
    if (lang === "eng") {
        return str
    } else {
        if (dictionary.hasOwnProperty(str)) {
            // @ts-ignore
            return dictionary[str]
        } else {
            return "?????????????????"
        }
    }
}

