import React, {FC} from "react";
import style from "./Pagination.module.scss";
import clsx from "clsx";
import {useAppSelector} from "../../../redux-toolkit-store/hooks";
import {selectThemeType} from "../../../redux-toolkit-store/appSlice";

interface IPagination {
    dotsNumber: number
    onClick: (index: number) => void
    currentIndex: number
    className?: string
}

export const Pagination: FC<IPagination> = ({
                                                dotsNumber,
                                                onClick,
                                                currentIndex,
                                                className
}) => {
    const dots = [];
    for (let i = 0; i < dotsNumber; i++) {
        dots.push(i);
    }
    const themeType = useAppSelector(selectThemeType);

    return (
        <div className={clsx(style.pagination, className)}
             data-aos="zoom-in"
        >
            {
                dots.map((el) => (
                    <div key={el}
                        className={clsx({
                        [style.dot]: true,
                        [style.dot_light]: themeType === "light",
                        [style.dot_dark]: themeType === "dark",
                    })}
                         onClick={() => onClick(el)}
                    >
                        {
                            currentIndex === el &&
                            <div className={style.inner}/>
                        }
                    </div>
                ))
            }
        </div>
    )
}