import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {baseURL} from "./api";
import {IPortfolio} from "../types/portfolio.types";

export const portfolioApi = createApi({
    reducerPath: "portfolioApi",
    baseQuery: fetchBaseQuery({baseUrl: baseURL}),
    tagTypes: ["Portfolio", "Portfolios"],
    endpoints: (builder) => ({
        getAllPortfolios: builder.query<IPortfolio[], void>({
            query: () => "/portfolio",
            providesTags: ["Portfolios"],
        }),
        getPortfolioById: builder.query<IPortfolio, string>({
            query: (id) => `/portfolio/${id}`,
            providesTags: (result, error, id) => [{type: "Portfolio", id}],
        }),
        // createProduct: builder.mutation<string, ProductUpdateType>({
        //     query: (body) => ({
        //         url: "/",
        //         method: "POST",
        //         body: body,
        //     }),
        //     invalidatesTags: ["Products"],
        // }),
        //============================ <   ,  body -       >
        incrementCount: builder.mutation<string, {id: string}>({
            query: ({id}) => ({
                url: `/portfolio/count/${id}`,
                method: "PUT",
                body: id,
            }),
            invalidatesTags: (result, error, arg) => [{type: "Portfolio", id: arg.id}, "Portfolios"],
        }),
        // deleteProduct: builder.mutation<string, { id: string }>({
        //     query: (body) => ({
        //         url: `/${body.id}`,
        //         method: "DELETE",
        //         body: {},
        //     }),
        //     invalidatesTags: ["Products"],
        // }),
    })
})

export const {
    useGetAllPortfoliosQuery,
    useGetPortfolioByIdQuery,
    useIncrementCountMutation
} = portfolioApi
