import React, {FC} from "react";
import style from "./langSwitcher.module.scss"
import clsx from "clsx";
import {useAppDispatch, useAppSelector} from "../../../redux-toolkit-store/hooks";
import {LangEnum, selectLang, selectThemeType, setLang} from "../../../redux-toolkit-store/appSlice";


export const LangSwitcher: FC<{ className?: string }> = ({className}) => {
    const lang = useAppSelector(selectLang);
    const dispatch = useAppDispatch();
    const themeType = useAppSelector(selectThemeType);

    return (
        <div className={clsx({
            [style.langSwitcher]: true,
            [style.langSwitcher_light]: themeType === "light",
            [style.langSwitcher_dark]: themeType === "dark",
        }, className)}>

            <div className={clsx({
                [style.btn]: true,
                [style.btn_light]: themeType === "light",
                [style.btn_dark]: themeType === "dark",
                [style.btn_selected_light]: lang === LangEnum["eng"] && themeType === "light",
                [style.btn_selected_dark]: lang === LangEnum["eng"] && themeType === "dark",
            })}
                 onClick={() => dispatch(setLang(LangEnum["eng"]))
                 }
            >
                ENG
            </div>

            <div className={clsx({
                [style.btn]: true,
                [style.btn_light]: themeType === "light",
                [style.btn_dark]: themeType === "dark",
                [style.btn_selected_light]: lang === LangEnum["rus"] && themeType === "light",
                [style.btn_selected_dark]: lang === LangEnum["rus"] && themeType === "dark",

            })}
                 onClick={() => dispatch(setLang(LangEnum["rus"]))}
            >
                RUS
            </div>

        </div>
    )
}