import React from "react";
import style from "./header.module.scss";
import {Container} from "../common/Container/Container";
import {svgIcons} from "../../assets/svg/svgIcons";
import {ThemeSwitcher} from "../common/ThemeSwitcher/ThemeSwitcher";
import {Social} from "../common/Social/Social";
import {LangSwitcher} from "../common/LangSwitcher/LangSwitcher";
import {Links} from "../common/Links/Links";
import {BurgerButton} from "../common/BurgerButton/BurgerButton";
import {HashLink} from "react-router-hash-link";
import {useAppSelector} from "../../redux-toolkit-store/hooks";
import {selectThemeType, ThemeTypeEnum} from "../../redux-toolkit-store/appSlice";
import {LogoD} from "../common/LogoD/LogoD";
import {useMediaQuery} from "@mui/material";

export const Header = () => {
    const themeType = useAppSelector(selectThemeType);
    const isDesktop = useMediaQuery('(min-width:1200px)');
    const isTablet = useMediaQuery('(min-width:576px)');
    const isMobile = useMediaQuery('(min-width:320px)');
    const size = isDesktop
        ? 75
        : isTablet
            ? 60
            : isMobile ? 50 : 25;

    return (
        <header id="header">
            <Container classNameInner={style.header}>
                <HashLink to="/#header" className={style.logoBlock}>
                    <LogoD size={size}/>
                    {themeType === ThemeTypeEnum.light ? svgIcons.logoShortDark : svgIcons.logoShortLight}
                </HashLink>

                <div className={style.rightBlock}>

                    <div className={style.topRow}>
                        <div className={style.topRowLeft}>
                            <div data-aos="fade-down">
                                <ThemeSwitcher/>
                            </div>
                            <div data-aos="fade-left"
                                 style={{zIndex: 200}}
                            >
                                <BurgerButton/>
                            </div>

                        </div>

                        <div className={style.topRowRight}>
                            <div data-aos="fade-down">
                                <LangSwitcher className={style.langSwitcher}/>
                            </div>
                            <div data-aos="fade-down"
                                 className={style.socialWrapper}
                            >
                                <Social className={style.social}/>
                            </div>
                            {/*<Tel className={style.tel}/>*/}
                        </div>

                    </div>

                    <Links className={style.BottomRow}/>


                </div>
            </Container>

        </header>
    )
}