import React, {FC} from "react";
import style from "./ButtonAnimation.module.scss"
import clsx from "clsx";
import "./anim.scss"
import {useAppSelector} from "../../../redux-toolkit-store/hooks";
import { selectThemeType } from "../../../redux-toolkit-store/appSlice";

interface IButtonAnimation {
    className?: string
}

export const ButtonAnimation: FC<IButtonAnimation> = ({className}) => {
    const themeType = useAppSelector(selectThemeType);

    return (
        <div className={clsx(
            style.buttonAnimation,
            Boolean(className) && className
        )}>
            {
                [0, 1, 2, 3].map(item => (
                    <div key={item}
                         className={clsx(style.item, "item")}
                         style={{
                             animationDelay: `${item * 1000}ms`,
                             background: themeType === "light" ? "#000": "#FFF"
                         }}
                    />
                ))
            }
        </div>
    )
}