import React, {ButtonHTMLAttributes, DetailedHTMLProps, FC} from "react";
import {translate} from "../../../utils/lang";
import style from "./ButtonUI.module.scss";
import clsx from "clsx";
import { useAppSelector } from "../../../redux-toolkit-store/hooks";
import {selectLang} from "../../../redux-toolkit-store/appSlice";

type ButtonUIType = {
    text: string
    className?: string
    href?: string
} & DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>
& React.DetailedHTMLProps<React.AnchorHTMLAttributes<HTMLAnchorElement>, HTMLAnchorElement>

export const ButtonUI: FC<ButtonUIType> = ({text, className, href, ...props}) => {
    const lang = useAppSelector(selectLang);

    return (
        <>
            {
                href ? (
                    <a href={href} className={clsx(style.buttonUI, className)} {...props}
                    >
                        {translate(text, lang)}
                    </a>
                ) : (
                    <button className={clsx(style.buttonUI, className)} {...props}
                    >
                        {translate(text, lang)}
                    </button>
                )
            }
        </>
    )
}