import React, {FC, memo} from "react";
import style from "./Portfolio.module.scss"
import {Container} from "../common/Container/Container";
import {H2} from "../common/H2/H2";
import img0 from "../../assets/png/portfolio/0.png";
import img1 from "../../assets/png/portfolio/1.png";
import img2 from "../../assets/png/portfolio/2.png";
import img3 from "../../assets/png/portfolio/3.png";
import img4 from "../../assets/png/portfolio/4.png";
import {HashLink} from "react-router-hash-link";
import {translate} from "../../utils/lang";
import clsx from "clsx";
import { useAppSelector } from "../../redux-toolkit-store/hooks";
import {selectLang, selectThemeType} from "../../redux-toolkit-store/appSlice";
import {useGetAllPortfoliosQuery} from "../../api/portfolio.api";

interface IPortfolio {
    title: string
    withButton?: boolean
}


export const Portfolio: FC<IPortfolio> = ({
                                              title,
                                              withButton = true
                                          }) => {
    const lang = useAppSelector(selectLang);
    const themeType = useAppSelector(selectThemeType);
    const {data, isFetching} = useGetAllPortfoliosQuery();

    return (
        <Container classNameInner={style.portfolio}>
            <H2 text={title}/>

            {
                data &&
                <div className={style.images}
                    //data-aos="fade-up"
                >
                    <div className={style.inner}>
                        <div className={style.left}>
                            <div className={style.row}>
                                <HashLink to={`/portfolio/0/#header`}
                                          data-aos="zoom-in"
                                >
                                    <img src={data[0].imgs.main} alt=""/>
                                </HashLink>
                                <HashLink to={`/portfolio/1/#header`}
                                          data-aos="zoom-in"
                                          //data-aos-delay="300"
                                >
                                    <img src={data[1].imgs.main} alt=""/>
                                </HashLink>
                            </div>
                            <div className={style.row}>
                                <HashLink to={`/portfolio/2/#header`}
                                          data-aos="zoom-in"
                                          //data-aos-delay="200"
                                >
                                    <img src={data[2].imgs.main} alt=""/>
                                </HashLink>
                                <HashLink to={`/portfolio/3/#header`}
                                          data-aos="zoom-in"
                                          //data-aos-delay="100"
                                >
                                    <img src={data[3].imgs.main} alt=""/>
                                </HashLink>
                            </div>
                        </div>
                        <HashLink to={`/portfolio/${4}`}
                                  className={style.right}
                                  data-aos="zoom-in"
                                  //data-aos-delay="400"
                        >
                            <img src={data[4].imgs.mobile} alt=""/>
                        </HashLink>
                    </div>
                </div>
            }

            {
                withButton &&
                <HashLink to="/portfolio/#header"
                          className={clsx({
                              [style.btn]: true,
                              [style.btn_light]: themeType === "light",
                              [style.btn_dark]: themeType === "dark",
                          })}
                          data-aos="fade-up"
                >
                    <p>{translate("See more", lang)}</p>
                </HashLink>
            }
        </Container>
    )
}

export const PortfolioMemo = memo(Portfolio);