import React, {FC} from "react";
import style from "./LogoD.module.scss";
import clsx from "clsx";
import "./logoD.scss"
import {svgIcons} from "../../../assets/svg/svgIcons";
import {useAppSelector} from "../../../redux-toolkit-store/hooks";
import {selectThemeType} from "../../../redux-toolkit-store/appSlice";

interface ILogoD {
    size?: number
}

export const LogoD: FC<ILogoD> = ({size = 100}) => {
    const themeType = useAppSelector(selectThemeType);

    const styleLogoD = {
        height: `${size}px`,
        width: `${size}px`,
    }

    return (
        <div className={style.logoD}
             style={styleLogoD}
        >
            <div className={clsx({
                [style.cube]: true,
                [style.cube_light]: themeType === "light",
                [style.cube_dark]: themeType === "dark",
            }, "logoD")}
                 style={styleLogoD}
            >
                <div style={{ transform: `translateZ(${0.5 * size}px)` }}>
                    {svgIcons.logoD}
                </div>

                <div style={{ transform: `translateZ(-${0.5 * size}px) rotateY(180deg)` }}>
                    {svgIcons.logoD}
                </div>

                <div style={{
                    transform: `rotateY(-270deg) translateX(${0.5 * size}px)`,
                    transformOrigin: "top right"
                }}>
                    {svgIcons.logoD}
                </div>

                <div style={{
                    transform: `rotateY(270deg) translateX(-${0.5 * size}px)`,
                    transformOrigin: "left"
                }}>
                    {svgIcons.logoD}
                </div>

                <div style={{
                    transform: `rotateX(-270deg) translateY(-${0.5 * size}px)`,
                    transformOrigin: "top center"
                }}>
                    {svgIcons.logoD}
                </div>

                <div style={{
                    transform: `rotateX(270deg) translateY(${0.5 * size}px)`,
                    transformOrigin: "bottom center"
                }}>
                    {svgIcons.logoD}
                </div>

            </div>
        </div>
    )
}