import {H2} from "../../common/H2/H2";
import clsx from "clsx";
import {HashLink} from "react-router-hash-link";
import {Container} from "../../common/Container/Container";
import React, {useState} from "react";
import style from "./PortfolioItems.module.scss"
import {translate} from "../../../utils/lang";
import {useAppSelector} from "../../../redux-toolkit-store/hooks";
import {selectLang, selectThemeType} from "../../../redux-toolkit-store/appSlice";
import {useGetAllPortfoliosQuery} from "../../../api/portfolio.api";
import {Preloader} from "../../common/Preloader/Preloader";
import {PortfolioCategoryType} from "../../../types/portfolio.types";
import {svgIcons} from "../../../assets/svg/svgIcons";

const menuItems = [
    "All",
    "Web",
    "Mobile",
    "Games",
    "Advertising",
];

export const PortfolioItems = () => {
    const [selectedType, setSelectedType] = useState("All");

    const themeType = useAppSelector(selectThemeType);

    const lang = useAppSelector(selectLang);

    const {data, isFetching} = useGetAllPortfoliosQuery();

    const selectedItems = data
        ? data
            .filter(portfolio => selectedType === "All"
                ? true
                : portfolio.category.includes(selectedType as PortfolioCategoryType))
        : [];

    return (
        <Container classNameInner={style.portfolioItems}>

            {isFetching && <Preloader/>}

            <H2 text="Portfolio"/>

            <div className={style.menu} data-aos="zoom-in">
                <div className={style.inner}>
                    {
                        menuItems.map((type, index) => (
                            <div key={type}
                                 className={clsx({
                                     [style.item]: true,
                                     [style.item_light]: themeType === "light",
                                     [style.item_dark]: themeType === "dark",
                                     [style.item_selected_light]: themeType === "light" && type === selectedType,
                                     [style.item_selected_dark]: themeType === "dark" && type === selectedType,
                                 })}
                                 onClick={() => setSelectedType(type)}
                                 // data-aos-duration="1000"
                                 // data-aos-delay={`${200 * index}`}
                            >
                                <p>{translate(type, lang)}</p>
                            </div>
                        ))
                    }
                </div>
            </div>

            {
                data &&
                <div className={style.items}>
                    {
                        selectedItems.map(({id, imgs, name, description, viewCount}, index) => (
                            <div key={id}
                                 className={style.item}>
                                <HashLink to={`/portfolio/${id}/#header`}
                                          className={style.imgWrapper}
                                          data-aos={( (index + 1) % 3 === 1 || (index + 1) % 3 === 0 ) ? "zoom-in-right" : "zoom-in-left"}
                                >
                                    <img src={imgs.main} alt=""/>
                                </HashLink>

                                <div className={style.textBlock}
                                     data-aos={( (index + 1) % 3 === 1 || (index + 1) % 3 === 0 ) ? "fade-left" : "fade-right"}
                                >
                                    <HashLink to={`/portfolio/${id}/#header`}
                                              className={style.title}
                                    >
                                        {name}
                                    </HashLink>

                                    <p className={style.text}>{description[lang]}</p>

                                    <div className={clsx({
                                        [style.countWrapper]: true,
                                        [style.countWrapper_light]: themeType === "light",
                                        [style.countWrapper_dark]: themeType === "dark",
                                    })}>
                                        {svgIcons.view}
                                        <p className={style.count}>{viewCount}</p>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </div>
            }

        </Container>
    )
}